import fetch from "isomorphic-unfetch";
import Link from "next/link";
import Layout from "../components/Layout";
import React, { Component } from "react";

class Index extends Component {
  static async getInitialProps() {
    const res = await fetch("http://localhost:5000/vendors"); // get the vendors API from backend routes/index.js
    const data = await res.json(); // store the result
    return { vendors: data }; // return the result
  }
 
  render() {
    const { vendors } = this.props; // decustructing the object 
  
    return (
      <Layout>
        {/* show all vendors information */}
        <section className="title-hero">

        <h1 className="text-center">Where next generations of vendors meet</h1>
        </section>
        <div className="vendors-wrapper container-fluid">
          {vendors.map(vendor => {
            const backgroundImage = {
               // backgroundImage: `url('/static/images/${vendor.shop_url}.jpeg')`,
               backgroundRepeat: "no-repeat",
               height: "400px",
               backgroundSize: "cover",
               background: `linear-gradient( to right, rgba(20, 20, 20, 0.6), rgba(20, 20, 20, 0.6)), url('/static/images/${vendor.shop_url}.jpeg')`
               
            }

            return (
              <div key={vendor.vendor_id} className="vendor-box" style={backgroundImage}>
                {/* <img src={vendor.logo} width="150px" /> */}
                <h2>{vendor.name}</h2>
                <p>{vendor.description}</p>
                {/* link to relative shop page information */}
                <Link as={`${vendor.shop_url}`} href={`/shop/?vendor_id=${vendor.vendor_id}`}>
              
                  <a>Vendor page</a>
                </Link>
              </div>
            );
          })}
        </div>
        <style jsx>
          {`
            h1 {
              margin-top: 120px;
              text-transform: capitalize;
            }
            h2{
              color: white
            }

            .vendors-wrapper {
                display: flex;
                flex-wrap: wrap;
                justify-content: center;
                margin-top: 60px;
                margin-bottom: 120px
              }
              .vendor-box{
                border: 2px solid #777;
                margin: 8px;
                width: 100%;
                border-radius: 4px;
                box-shadow: 0px 0px 10px 0px rgba(12, 5, 5, 0.6)
              }
              .title-hero{
                
                padding: 40px
                -webkit-box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.75);

              }

            @media only screen and (min-width: 1200px) {
            
              .vendor-box {
                width: 30%;
              }
            }
          `}
        </style>
      </Layout>
    );
  }
}

export default Index;
